<template>
	<div class="choiceness main-item" id="choiceness_bind">
		<h3></h3>
		<div class="choiceness-content">
			<div class="choiceness-item" @click="go_classify(item,index)" v-for="(item,index) in data">
				<a href="javascript:;">
					<img :src="item.icon" alt="">
					<span class="choiceness-item-desc">{{item.title}}</span>
				</a>
			</div>
		</div>
	</div>
</template>

<script>
	export default{
		name:'home-classify1',
		data(){
		    return {
				data:[]
			}	
		},
		methods:{
			go_classify(item,index){
				// console.log(item,index)
				this.$router.push({path:'classify',query:{index}})
			}
		},
		computed:{},
		components:{},
		mounted(){
			axios({
				url:'/api/goods/goods3'
			}).then(res=>{
				if(res.data.err===0){
					this.data=res.data.data[0].goods3
				}else{
					console.log('')
				}
			})
		},
		updated(){},
		props:{}
	}
</script>
