<template>
	<div class="classify">
		<top-blue :title="''"></top-blue>
		<classify-left @click="find" :selectItem="index"></classify-left>
		<classify-right></classify-right>
	</div>
</template>

<script>
	import $ from 'jquery'
	import TopBlue from '../components/top-blue.vue'
	import ClassifyLeft from '../components/classify-left.vue'
	import ClassifyRight from '../components/classify-right.vue'
	export default {
		name:'Classify',
		data(){
			return {
				index:1
			}
		},
		components:{
			TopBlue,
			ClassifyLeft,
			ClassifyRight
		},
		mounted(){
			if(!this.$route.query.index){
				this.index=1;
				$('html,body').stop().animate({scrollTop:0},500)
				return
			}
			setTimeout(()=>{
				this.index=parseInt(this.$route.query.index)+1;
				let scrollHeight=$('.store-item').eq(this.index-1).offset().top;
				$('html,body').stop().animate({scrollTop:scrollHeight-35},500)
			},1000)
		},
		methods:{
			find(item,ind){
				this.index=ind+1;
				let scrollHeight=$('.store-item').eq(ind).offset().top;
				if(ind==0){
					$('html,body').stop().animate({scrollTop:0},500)
				}else{
					$('html,body').stop().animate({scrollTop:scrollHeight-35},500)
				}
			}
		},

	}
</script>

