<template>
	<footer>
		<!-- tabbar -->
		<div class="weui-tabbar" id="footer-tabber" @click="Active">
			<a href='javascript:;' class="weui-tabbar__item weui-bar__item_on" @click="Goto1">
				<!--<span class="weui-badge" style="position: absolute;top: -.4em;right: 1em;">8</span>-->
				<div class="weui-tabbar__icon">
					<img src="../assets/images/icon_nav_button.png" alt="">
				</div>
				<p class="weui-tabbar__label"></p>
			</a>
			<!--<a href="javascript:;" class="weui-tabbar__item">-->
			<a href="javascript:;" class="weui-tabbar__item" @click="Goto2">
				<div class="weui-tabbar__icon">
					<img src="../assets/images/icon_nav_msg.png" alt="">
				</div>
				<p class="weui-tabbar__label"></p>
			</a>
			<a href="javascript:;" class="weui-tabbar__item" @click="Goto3">
				<div class="weui-tabbar__icon">
					<img src="../assets/images/icon_nav_article.png" alt="">
				</div>
				<p class="weui-tabbar__label"></p>
			</a>
			<a href="javascript:;" class="weui-tabbar__item" @click="Goto4">
				<div class="weui-tabbar__icon">
					<img src="../assets/images/icon_nav_cell.png" alt="">
				</div>
				<p class="weui-tabbar__label"></p>
			</a>
		</div>
	</footer>
</template>
<script>
	import $ from 'jquery'
	export default{
		name:'Footer',
		data(){
			return {
				
			}
		},
		methods:{
			Goto1(){
				this.$router.push('/home');
			},
			Goto2(){
				this.$router.push('/classify');
			},
			Goto3(){
				this.$router.push('/find/all');
			},
			Goto4(){
				this.$router.push('/mine');
			},
			Active(){
				$('body').on('click','.weui-tabbar__item',function(){
					$(this).addClass('weui-bar__item_on');
					$(this).siblings().removeClass('weui-bar__item_on')
				})
			},
			Update(path){
				if(path.includes('home')){
					$('.weui-tabbar a').eq(0).addClass('weui-bar__item_on').siblings().removeClass('weui-bar__item_on');
				}
				if(path.includes('classify')){
					$('.weui-tabbar a').eq(1).addClass('weui-bar__item_on').siblings().removeClass('weui-bar__item_on')
				}
				if(path.includes('find')){
					$('.weui-tabbar a').eq(2).addClass('weui-bar__item_on').siblings().removeClass('weui-bar__item_on');
				}
				if(path.includes('mine')){
					$('.weui-tabbar a').eq(3).addClass('weui-bar__item_on').siblings().removeClass('weui-bar__item_on');
				}
			}
		},
		computed:{},
		components:{},
		mounted(){
		},
		watch:{
			$route:{
				handler(nextValue){
				  let path=nextValue.path;
				  this.Update(path);
				}
			}
		},
		props:{}
	}
</script>
<style scoped>
	footer{
		width: 100%;
		display: block;
		position: fixed;
		bottom: 0;
		z-index: 10;
	}
	.weui-tabbar{
	  display:flex;
	  position:absolute;
	  z-index:500;
	  bottom:0;
	  width:100%;
	  background-color:#F7F7FA;
	}
	.weui-tabbar:before{
	  content:" ";
	  position:absolute;
	  left:0;
	  top:0;
	  right:0;
	  height:1px;
	  border-top:1px solid #C0BFC4;
	  color:#C0BFC4;
	  transform-origin:0 0;
	  transform:scaleY(0.5);
	}
	.weui-tabbar__item{
	  display:block;
	  flex:1;
	  padding:5px 0 0;
	  font-size:0;
	  color:#999999;
	  text-align:center;
	  -webkit-tap-highlight-color:rgba(0, 0, 0, 0);
	}
	.weui-tabbar__item.weui-bar__item_on .weui-tabbar__icon,
	.weui-tabbar__item.weui-bar__item_on .weui-tabbar__icon > i,
	.weui-tabbar__item.weui-bar__item_on .weui-tabbar__label{
	  color:#09BB07;
	}
	.weui-tabbar__icon{
	  display:inline-block;
	  width:27px;
	  height:27px;
	}
	i.weui-tabbar__icon,
	.weui-tabbar__icon > i{
	  font-size:24px;
	  color:#999999;
	}
	.weui-tabbar__icon img{
	  width:100%;
	  height:100%;
	}
	.weui-tabbar__label{
	  text-align:center;
	  color:#999999;
	  font-size:10px;
	  line-height:1.8;
	}
</style>
