<template>
	<div class="confrim-address">
		<top-blue :title="''"></top-blue>
		<div class="address-content">
			<div class="address-inf">
				<span class="user-name inf-item">{{address.name}}</span>
				<span class="user-tel inf-item">{{address.phone}}</span>
			</div>
			<div class="user-add">
				<a href="./address_list.html">
					<span class="add-inf">{{address.address}}</span>
					<i class="icon-right"></i>
				</a>
			</div>
		</div>
		
		<div class="pro-content">
			<div class="pro-img">
				<a href="#">
					<img :src="data.hot_icon" alt="">
				</a>
			</div>
			<div class="pro-intro">
				<div class="pro-name pro-item">{{data.hot_title}}</div>
				<div class="pro-desc pro-item">{{data.hot_intr1}}</div>
				<div class="pro-pri pro-item">
					<span><i></i><span class="pro-price">{{data.hot_price}}</span></span>
					<div class="pro-num">
						<i class="reduce symbol-item" @click="jian">-</i>
						<span class="num order_num">1</span>
						<i class="add symbol-item" @click="add">+</i>
					</div>
				</div>
			</div>
		
		</div>
		<footer>
		    <div class="order-bar">
		        <span class="order-total"></span>
		        <a href="#" class="order-totalPri">{{data.hot_price}}</a>
		        <a class="order-sub" @click="CheckOut"></a>
		    </div>
		</footer>
		<div class="pay" v-if="code===1">
			<div class="box">
				<div class="b-top">
					<span></span>
					<i @click="close"></i>
				</div>
				<div class="btn">
					<button @click="confirm"></button>
					<button @click="cancel"></button>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import TopBlue from '../components/top-blue.vue'
	export default {
		name:'confrim-address',
		data(){
			return {
				data:{},
				data1:{},
				code:0,
				id:'',
				address:{}
			}
		},
		components:{
			TopBlue
		},
		mounted(){
			let username=JSON.parse(localStorage.getItem('user')).data.username;
			let id=JSON.parse(localStorage.getItem('user')).data._id;
			axios({
				url:'/api/user',
				params:{
					username:username,
					_id:id
				}
			}).then(res=>{
				this.address=res.data.data.address[0]
				// console.log(res.data.data.address[0])
			})
			axios({
				url:`/api/goods/${this.$route.query.collection}`
			}).then(res=>{
				if(res.data.err==0){
					if(this.$route.query.code=='classify'){
						res.data.data[0].goods3.forEach((item,index)=>{
							if(item.title===this.$route.query.title){
								this.data1=item.goods[this.$route.query.index].detail
								// console.log(item)
								this.data={}
							}
						})
						this.data.hot_title=this.data1.shop_title;
						this.data.hot_intr1=this.data1.shop_intr1;
						this.data.hot_intr2=this.data1.shop_intr2;
						this.data.hot_icon=this.data1.shop_icon;
						this.data.hot_price=this.data1.shop_price;
						console.log(this.data)
					}else{
						res.data.data.forEach((item)=>{
							if(item.title===this.$route.query.title){
								this.data=item.detail;
							}
						})
					}
				}
			})
		},
		methods:{
			add(){
				let num = document.querySelector('.num');
				let sum = ++num.innerHTML;
				this.sum(sum);
			},
			jian(){
				let num = document.querySelector('.num');
				if(num.innerHTML!=1){
					let sum = --num.innerHTML;
					this.sum(sum);
				}
			},
			sum(num){
				let sumPrice = document.querySelector('.order-totalPri');
				sumPrice.innerHTML=num*this.data.hot_price;
			},
			close(){
				this.code=0;
			},
			CheckOut(){
				this.code=1;
			},
			confirm(){
				let username = JSON.parse(localStorage.getItem('user')).data.username;
				let num = document.querySelector('.num');
				let sumPrice = document.querySelector('.order-totalPri');
				// console.log(num.innerHTML);
				this.data.num = num.innerHTML;
				this.data.hot_price = sumPrice.innerHTML;
				this.data.id='receiveing';
				let code = {
					paying:false,
					receiveing:true,
					finished:false
				};
				this.data.code=code;
				console.log(this.data)
				axios({
					url:'/api/other/order',
					method:'post',
					data:{
						username:username,
						order:this.data
					}
				}).then(res=>{
					console.log(res.data)
					if(res.data.err==0){
						this.code=0;
						this.$router.push('/find/all');
					}
				})
			},
			cancel(){
				let username = JSON.parse(localStorage.getItem('user')).data.username;
				let num = document.querySelector('.num');
				let sumPrice = document.querySelector('.order-totalPri');
				// console.log(num.innerHTML);
				this.data.num = num.innerHTML;
				this.data.hot_price = sumPrice.innerHTML;
				this.data.id='paying';
				let code = {
					paying:true,
					receiveing:false,
					finished:false
				};
				this.data.code=code;
				axios({
					url:'/api/other/order',
					method:'post',
					data:{
						username:username,
						order:this.data
					}
				}).then(res=>{
					console.log(res.data)
					if(res.data.err==0){
						this.code=0;
						this.$router.push('/find');
					}
				})
			}
		}
	}
</script>

<style scoped>
	.address-content {
	  background-color: #fff;
	  padding: .1rem;
	  margin-top: .44rem;
	}
	.address-content .address-inf {
	  font-size: 0px;
	}
