	  border-top: 1px solid #ccc;
	  border-bottom: 1px solid #ccc;
	  display: flex;
	  font-size: 14px;
	  text-align: center;
	}
	.pro-content .pro-intro .pro-pri .pro-num .num {
	  flex: auto;
	}
	.pro-content .pro-intro .pro-pri .pro-num .symbol-item {
	  width: .3rem;
