<template>
  <div id="app">
	  <!-- <login></login>-->
	  <!-- <reg></reg> -->
	  <router-view ></router-view>
	  <!-- <mine></mine> -->
	  <Footer v-show="$root.bFoot"></Footer>
  </div>
</template>
<script>
import Home from './pages/Home.vue'
import Classify from './pages/Classify.vue'
import Find from './pages/Find.vue'
import Mine from './pages/Mine.vue'
import AddressCont from './pages/Address-cont.vue'
import AddAddress from './pages/Add-Address.vue'
import Footer from './layouts/footer.vue'
import Login from './pages/Login.vue'
import Reg from './pages/Reg.vue'
import FindPassword from './pages/FindPassword.vue'
import ModifyMes from './pages/Modify-Mes.vue'
export default {
  name: 'App',
  components: {
	  Home,
	  Classify,
	  Footer,
	  Find,
	  Mine,
	  AddressCont,
	  AddAddress,
	  Login,
	  Reg,
	  FindPassword,
	  ModifyMes
  },
  methods:{
	 checkRootData(path){
		 // console.log(path)
		if(/home|find|classify|mine/.test(path)){
		  this.$root.bFoot=true;
		}
		if(/modify-mes|detail|address-cont|add-address|login|reg|findpassword/.test(path)){
			this.$root.bFoot=false;
		}
		if(path.includes('detail')){
			this.$root.bFoot=false;
		}
		if(path.includes('confirm-address')){
			this.$root.bFoot=false;
		}
		//loading 
		// if(path.includes('user')){
		//  this.$store.commit(this.$types.UPDATE_NAV,false)
		//  this.$store.commit(this.$types.UPDATE_FOOT,true)
		// }
	  } 
  },
  watch:{
	$route:{
	  handler(nextValue){
		  let path=nextValue.path;
		  this.checkRootData(path);
	  }
	}
  }
}
</script>

