<template>
	<div class="add-address">
		<top-blue :title="''"></top-blue>
		<uc-cell4 class="name"></uc-cell4>
		<uc-cell4 class="phone" :cont="''"></uc-cell4>
		<uc-cell4 class="address" :cont="''"></uc-cell4>
		<Button :val="''" @fn="AddAddress"></Button>
	</div>
</template>

<script>
	import TopBlue from '../components/top-blue.vue'
	import UcCell4 from '../components/uccell4.vue'
	import Button from '../components/button.vue'
	export default {
		name:'add-address',
		components:{
			TopBlue,
			UcCell4,
			Button
		},
		data(){
			return {
				data:{}
			}
		},
		methods:{
			AddAddress(){
				console.log()
				let username=JSON.parse(localStorage.getItem('user')).data.username;
				let id=JSON.parse(localStorage.getItem('user')).data._id;
				let mes1 = document.querySelector('.name input')
				let mes2 = document.querySelector('.phone input')
				let mes3 = document.querySelector('.address input')
				let mes4 = this.$route.query.id;
				this.data.name=mes1.value;
				this.data.phone=mes2.value;
				this.data.address=mes3.value;
				this.data.id=mes4;
				axios({
					url:'/api/other/address',
					method:'post',
					data:{
						username:username,
						_id:id,
						address:this.data
					}
				}).then(res=>{
					if(res.data.err==0){
						console.log(res.data)
						this.$router.push('/address-cont')
					}else{
						console.log('');
					}
				})
			}
		}
	}
</script>

<style scoped>
	.add-address{
		margin-top: .49rem;
	}
</style>
