<template>
	<div class="home">
		<search></search>
		<banner></banner>
		<notice></notice>
		<home-classify1></home-classify1>
		<home-classify2 :data="list2" :collection="goods1"></home-classify2>
		<home-classify3></home-classify3>
		<back-top></back-top>
	</div>
</template>

<script>
	import Search from '../components/search.vue'
	import Banner from '../components/banner/index.vue'
	import Notice from '../components/notice.vue'
	import HomeClassify1 from '../components/home-classify1.vue'
	import HomeClassify2 from '../components/home-classify2/index.vue'
	import HomeClassify3 from '../components/home-classify3.vue'
	import BackTop from '../components/BackTop.vue'
	export default {
		name:'home',
		components:{
			Search,
			Banner,
			Notice,
			HomeClassify1,
			HomeClassify2,
			HomeClassify3,
			BackTop
		},
		data(){
			return {
				goods1:'goods1',
				banner:[],
				list1:[],
				list2:[],
				list3:[]
			}
		},
		mounted(){
			setTimeout(()=>{
				axios({
					url:'/api/goods/goods1'
				}).then(res=>{
					if(res.data.err==0){
						this.list2=res.data.data;
					}
				})
			},500)
		}
	}
</script>

