<template>
	<div class="mine">
		<top-blue :title="''"></top-blue>
		<img-icon :src="$baseUrl+user.icon" :nikename="user.nikename"></img-icon>
		<uc-cell2 @click="MyOrder"></uc-cell2>
		<uc-cell2 @click="MyAddress" :cont="''"></uc-cell2>
		<uc-cell2 :cont="''"></uc-cell2>
		<Button :val="''" @fn="login"></Button>
	</div>
</template>
<script>
	// import axios from '../plugins/axios.js'
	import TopBlue from '../components/top-blue.vue'
	import UcCell2 from '../components/uccell2.vue'
	import Button from '../components/button.vue'
	import ImgIcon from '../components/ImgIcon.vue'
	export default {
		name:'mine',
		data(){
			return {
				user:{}
			}
		},
		mounted(){
			axios.get('/api/user')
			.then(
				res=>{
					res.data.err===0?this.user=res.data.data:this.$router.push('/login')
				}
			)
		},
		components:{
			Button,
			UcCell2,
			TopBlue,
			ImgIcon
		},
		methods:{
			login(){
				this.$router.push('/login');
			},
			MyOrder(){
				this.$router.push('/find/all')
			},
			MyAddress(){
				this.$router.push('/address-cont')
			}
		}
	}
</script>

