<template>
	<div class="store-menu">
		<div class="store-content" id="store_bind">
			<div class="store-item" v-for="(item,index) in data" :key="index">
				<h3>{{item.title}}</h3>
				<ul class="store-item-content">
					<li  v-for="(pro,index) in item.goods" :key="index">
						<div class="item-img">
							<img  :src="pro.icon">
						</div>
						<div class="item-desc">
							<div class="item-title">{{pro.title}}</div>
							<div class="item-des">{{pro.sub_title}}</div>
							<div class="item-price">
								<div class="item-newPrice"><span>:</span><span class="newPrice">{{pro.prcie}}</span> </div>
								<!-- <div class="item-oldPrice"><span>:</span><span class="oldPrice">{{pro.oldPrice}}</span></div> -->
							</div>
						</div>
						<a class="bug" href="javascript:;" @click="confirm(item,index)"></a>
					</li>
				</ul>
			</div>
		</div>
	</div>
</template>
<script>
	export default{
		name:'',
		data(){
			return {
				data:[]
			}	
		},
		methods:{
			confirm(item,index){
				// console.log(this.data1)
				this.$router.push({path:'/detail',query:{collection:'goods3',code:'classify',title:item.title,index:index}})
			}
		},
		computed:{},
		components:{},
		mounted(){
			axios({
				url:'/api/goods/goods3'
			}).then(res=>{
				if(res.data.err==0){
					this.data=res.data.data[0].goods3;
				}
			})
		},
		updated(){},
		props:{}
	}
</script>
