<template>
	<div class="order_content" id="order-content-bind">
		<div class="order_product" v-for="(item,index) in data">
			<div class="store_inf">
				<div class="store_logo">
					<a href="#">
						<img v-if="item.hot_icon" :src="item.hot_icon" alt="">
					</a>
				</div>
				<div class="store_name" v-if="item.hot_title">{{item.hot_title}}</div>
				<div class="pro_status">{{item.id}}</div>
			</div>
			<div class="pro_content">
				<div class="pro_img">
					<a href="#">
						<img :src="item.hot_icon" alt="">
					</a>
				</div>
				<div class="pro_inf">
					<div class="pro_name">{{ item.hot_title }}</div>
					<div class="pro_desc">{{ item.hot_intr1 }}</div>
				</div>
			</div>
			<div class="pro_total_price" v-if="item.products !=''"> <span>{{item.hot_price}}</span></div>

			<!-- <div class="pro_operation" v-if="item.code !== ''">
				<a href="#"></a>
			</div> -->
			<div class="pro_operation" v-if="item.code.paying === true">
				<a href="javascript:;" @click="pay(index)"><i  v-show="status!==0">{{index}}</i></a>
				<a class="deleteOrder" @click="del(item)" href="javascript:;"></a>
			</div>
			<div class="pro_operation" v-if="item.code.receiveing === true">
				<a href="javascript:;" @click="receive"></a>
			</div>
			<div class="pro_operation" v-if="item.code.finished === true">
				<a href="javascript:;"></a>
			</div>
		</div>
		<div class="pay" v-if="code===1">
			<div class="box">
				<div class="b-top">
					<span></span>
					<i @click="close"></i>
				</div>
				<div class="btn">
					<button @click="confirm"></button>
					<button @click="cancel"></button>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
	export default{
		name:'order-cont',
		data(){
			return {
				code:0,
				status:0,
				judge:0,
				type:''
			}
		},
		methods:{
			pay(index){
				this.code=1;
				this.judge=index;
				this.type='pay';
			},
			del(){
				this.code=1;
			},
			receive(){
				this.code=1;
				this.type='receive';
			},
			close(){
				this.code=0;
			},
			confirm(){
				let username=JSON.parse(localStorage.getItem('user')).data.username;
				let id=JSON.parse(localStorage.getItem('user')).data._id;
				if(this.type==='pay'){
					axios({
						url:'/api/other/order',
						data:{
							username:username,
							_id:id,
							paying:false
						},
						method:'post'
					}).then(res=>{
						if(res.data.err==0){
							console.log(res.data)
						}else{
							alert('');
						}
					})
				}
				if(this.type==='receive'){
					axios({
						url:'/api/other/order',
						data:{
							username:username,
							_id:id,
							receiveing:false
						},
						method:'post'
					}).then(res=>{
						if(res.data.err==0){
							console.log(res.data)
						}else{
							alert('');
						}
					})
				}
				this.code=0;
			},
			cancel(){
				this.code=0;	
			}
		},
		computed:{},
		components:{},
		mounted(){
		},
		updated(){},
		props:['data']
	}
</script>
<style scoped>
	.order_content {
	  display: flex;
	  flex-direction: column;
	}
	.order_content .order_product {
	  margin: .05rem 0 0;
	  background-color: #fff;
	}
	.order_content .order_product .store_inf {
	  line-height: .4rem;
	  border-bottom: 1px solid #cccccc;
	  display: flex;
	  flex-direction: row;
	  text-align: center;
	  padding: .05rem;
	}
	.order_content .order_product .store_inf .store_logo {
	  width: .5rem;
	}
	.order_content .order_product .store_inf .store_logo a img {
	  display: inline-block;
	  width: 80%;
	  border-radius: 50%;
	}
	.order_content .order_product .store_inf .store_name {
	  flex: 1 1 auto;
	  text-align: left;
	  padding-left: .1rem;
	}
	.order_content .order_product .store_inf .pro_status {
	  width: .8rem;
	  color: red;
	}
	.order_content .order_product .pro_content {
	  display: flex;
	  flex-direction: row;
	  padding: .1rem 0;
	  border-bottom: 1px solid #ccc;
	}
	.order_content .order_product .pro_content .pro_img {
	  flex: 1 1 30%;
	  width: 30%;
	}
	.order_content .order_product .pro_content .pro_img a {
	  display: flex;
	  justify-content: center;
	}
	.order_content .order_product .pro_content .pro_img a img {
	  width: 80%;
	  height: 80%;
	  justify-content: center;
	  align-items: center;
	}
	.order_content .order_product .pro_content .pro_inf {
	  width: 70%;
	}
	.order_content .order_product .pro_content .pro_inf .pro_name {
	  width: 90%;
	  margin-right: .15rem;
	  overflow: hidden;
	  text-overflow: ellipsis;
	  white-space: nowrap;
	  font-size: 14px;
	  font-weight: 700;
	  margin-bottom: .1rem;
	}
	.order_content .order_product .pro_content .pro_inf .pro_desc {
	  width: 90%;
	  display: -webkit-box;
	  -webkit-box-orient: vertical;
	  -webkit-line-clamp: 2;
	  overflow: hidden;
	  font-size: 12px;
	}
	.order_content .order_product .pro_total_price {
	  line-height: .3rem;
	  text-align: right;
	  font-size: 14px;
	  border-bottom: 1px solid #ccc;
	  padding-right: .2rem;
	}
	.order_content .order_product .pro_operation {
	  margin: .1rem;
	  text-align: right;
	  font-size: 0;
	}
	.order_content .order_product .pro_operation a {
	  display: inline-block;
	  padding: .05rem .15rem;
	  background-color: #2d9cff;
	  color: #fff;
	  border-radius: .05rem;
	  font-size: 12px;
	  margin-left: 0.1rem;
	}
	.pay{
		width: 100%;
		height: 100%;
		position: fixed;
		top:0;left:0;right:0;bottom:0;
		background-color: #ccc;
		opacity: .8;
		z-index:20
	}
	.pay .box{
		width: 60%;
		height:1.8rem;
		position: fixed;
		top:50%;
		left:50%;
		margin-top:-.9rem;
		margin-left:-30%;
		background-color: #fff;
		z-index:21;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		border-radius: 10px;
		border:1px solid #ccc;
	}
	.pay .box .b-top{
		width: 100%;
		height: 20px;
		line-height: 20px;
		display: flex;
		justify-content: center;
		position: relative;
	}
	.pay .box .b-top span{
		color:#000000;
		font-weight: bold;
		font-size: 14px;
	}
	.pay .box .b-top i{
		position: absolute;
		right: 5px;
		font-size: 26px;
		line-height: 20px;
		color: red;
	}
	.pay .box .btn{
		width: 100%;
		margin-top: .5rem;
		display: flex;
		justify-content: center;
	}
	.pay .box .btn button{
		flex:1;
		margin:0 .3rem;
		height: .5rem;
		background-color: #9FCDFF;
		border-radius: 5px;
	}
</style>
