<template>
	<div class="order">
		<div class="order_nav" id="order-menu-bind">
			<router-link  class="order_item"
			   to="/find/all"
			   active-class="order_item_active"
			   @click.native="jump"
			   >
			   
			</router-link>
			<router-link  class="order_item"
			   to="/find/paying"
			   active-class="order_item_active"
			   @click.native="jump"
			   >
			   
			</router-link><router-link  class="order_item"
			   to="/find/receiving"
			   active-class="order_item_active"
			   @click.native="jump"
			   >
			   
			</router-link><router-link  class="order_item"
			   to="/find/finished"
			   active-class="order_item_active"
			   @click.native="jump"
			   >
			   
			</router-link>
			<!-- <a href="javascript:;" class="order_item"></a> -->
		</div>
		<order-cont :data="data"></order-cont>
	</div>
	</template>
<script>
	import OrderCont from './order-cont.vue'
	export default{
		name:'order-nav',
		data(){
			return{
				data:[]
			}
		},
		mounted(){
			let username=JSON.parse(localStorage.getItem('user')).data.username;
			let id=JSON.parse(localStorage.getItem('user')).data._id;
			axios({
				url:"/api/user",
				params:{
					username:username,
					_id:id
				}
			}).then(res=>{
				if(res.data.err==0){
					this.data=[];
					this.data=res.data.data.order;
					}
				})
		},
		methods:{
			jump(){
				let path=this.$route.params._id;
				let username=JSON.parse(localStorage.getItem('user')).data.username;
				let id=JSON.parse(localStorage.getItem('user')).data._id;
				axios({
					url:"/api/user",
					params:{
						username:username,
						_id:id
					}
				}).then(res=>{
					if(res.data.err==0){
						if(path.includes('all')){
							this.data=[];
							this.data=res.data.data.order;
							console.log(this.data)
						}
						if(path.includes('paying')){
							this.data=[];
							res.data.data.order.forEach(item=>{
								if(item.code.paying==true){
									this.data.push(item);
								}
							})
						}
						if(path.includes('receiving')){
							this.data=[];
							res.data.data.order.forEach(item=>{
								if(item.code.receiveing===true){
									this.data.push(item);
								}
							})
						}
						if(path.includes('finished')){
							this.data=[];
							res.data.data.order.forEach(item=>{
								if(item.code.finished===true){
									this.data.push(item);
								}
							})
						}
					}else{
						console.log('');
					}
				})
			}
		},
		computed:{},
		components:{
			OrderCont
		},
	}
</script>
<style scoped>
	.order_nav {
	  display: flex;
	  flex-direction: row;
	  width: 100%;
	  height: .4rem;
	  line-height: .4rem;
	  border-bottom: 1px solid #ccc;
	  margin-top: .44rem;
	}
	.order_nav a.order_item {
	  flex: 1 ;
	  width: 25%;
	  text-align: center;
	  border-right: 1px solid #cccccc;
	  background-color: #fff;
	  border-bottom: 2px solid transparent;
	}
	.order_nav a.order_item:last-child {
	  border-right: none;
	}
	.order_nav a.order_item_active {
	  border-bottom: 2px solid #2d9cff;
	}
</style>
