<template>
	<div class="address-cont">
		<top-blue  :title="''"></top-blue>
		<show-address :data="data" ></show-address>
		<Button :val="''" @fn="AddAddress"></Button>
	</div>
</template>

<script>
	import TopBlue from '../components/top-blue.vue'
	import ShowAddress from '../components/show-address.vue'
	import Button from '../components/button.vue'
	export default {
		name:'address-cont',
		data(){
			return {
				data:[]
			}
		},
		components:{
			TopBlue,
			ShowAddress,
			Button
		},
		methods:{
			AddAddress(){
				let showadd=document.querySelector('.address-content');
				// console.log(showadd.children.length)
				// showadd.children.length
				this.$router.push({path:'/add-address',query:{id:showadd.children.length}})
			}
		},
		mounted(){
			axios({
				url:'/api/user'
			}).then(res=>{
				if(res.data.err==0){
					this.data=res.data.data.address;
				}else{
					alert('')
				}
			})
		}
	}
</script>

