<template>
	<div class="detail">
		<top-blue :title="''"></top-blue>
		<div class="imgbox">
			<img :src="data.hot_icon" alt="">
		</div>
		<div class="detail-content">
			<div class="pro-title pro-item">{{data.hot_title}}</div>
			<div class="pro-desc pro-item">{{data.hot_intr1}}</div>
			<div class="pro-price pro-item">
				<span class="pro-newPrice">{{data.hot_price}}</span>
				<!-- <span class="pro-oldPrice">3600</span> -->
			</div>
			<div class="detail-intro pro-item">
				<p>{{data.hot_intr2}}</p>
			</div>
		</div>
		<Button :val="''" @fn="Buy"></Button>
	</div>
</template>
<script>
	import TopBlue from '../components/top-blue.vue'
	import Button from '../components/button.vue'
	export default{
		name:'detail',
		data(){
		    return {
				data:{},
				data1:{}
			}	
		},
		methods:{
			Buy(){
				// console.log(this.data)
				this.$router.push(
				{path:'/confirm-address',
				query:{title:this.$route.query.title,index:this.$route.query.index,collection:this.$route.query.collection,code:this.$route.query.code}}
				)
			}
		},
		computed:{},
		components:{
			TopBlue,
			Button
		},
		mounted(){
			if(this.$route.query.code=='classify'){
				axios({
					url:`/api/goods/${this.$route.query.collection}`
				}).then(res=>{
					if(res.data.err==0){
						// console.log(res.data.data[0].goods3)
						res.data.data[0].goods3.forEach((item,index)=>{
							if(item.title==this.$route.query.title){
								this.data1=item.goods[this.$route.query.index].detail
								this.data={}
							}
						})
						this.data.hot_title=this.data1.shop_title;
						this.data.hot_intr1=this.data1.shop_intr1;
						this.data.hot_intr2=this.data1.shop_intr2;
						this.data.hot_icon=this.data1.shop_icon;
						this.data.hot_price=this.data1.shop_price;
						// console.log(this.data)
					}
				})
			}
			else{
				axios({
					url:`/api/goods/${this.$route.query.collection}`
				}).then(res=>{
					if(res.data.err==0){
						res.data.data.forEach((item)=>{
							if(item.title===this.$route.query.title){
								this.data=item.detail;
							}
						})
					}
				})				
			}
		},
		updated(){
		},
		props:{}
	}
</script>
<style scoped>
	.imgbox{
		margin: 0 auto;
		position: relative;
		overflow: hidden;
		z-index: 1;
		margin-top:.44rem;
	}
	.imgbox img{
		width: 100%;
	}
	.detail-content {
	  display: flex;
	  background-color: #fff;
	  padding: .1rem;
	  flex-direction: column;
	  width: 100%;
	  margin-bottom: .2rem;
	}
	.detail-content .pro-title {
	  flex: 1;
	  line-height: .3rem;
	  font-size: 14px;
	}
	.detail-content .pro-desc {
	  flex: 1;
	  line-height: .3rem;
	}
	.detail-content .pro-price {
	  flex: 1;
	  height: .3rem;
	  line-height: .3rem;
	}
	.detail-content .pro-price .pro-oldPrice {
	  text-decoration: line-through;
	}
	.detail-content .detail-intro {
	  flex: 1;
	  line-height: .2rem;
	  padding: .2rem 0;
	}
	.detail-content .pro-item {
	  border-bottom: 1px solid #ccc;
	}
	/* .detail-content .pro-item  p{
		margin-top: .1rem;
	} */
</style>
