<template>
	<div class="reg">
		<top-blue :title="''"></top-blue>
		<login-icon :classname="'headicon'"></login-icon>
		<uc-cell1 :cont="''" v-model="username"></uc-cell1>
		<uc-cell1 :cont="''" :type="'password'" v-model="password"></uc-cell1>
		<uc-cell1 :cont="''" v-model="nikename"></uc-cell1>
		<span class="tips" v-show="errormes!==''">{{errormes}}</span>
		<Button :val="''" @fn="reg"></Button>
	</div>
</template>

<script>
	import $ from 'jquery'
	import TopBlue from '../components/top-blue.vue'
	import UcCell1 from '../components/uccell1.vue'
	import Button from '../components/button.vue'
	import LoginIcon from '../components/Login-icon.vue'
	export default {
		name:'reg',
		data(){
			return {
				username:'',
				password:'',
				nikename:'',
				errormes:''
			}
		},
		components:{
			TopBlue,
			UcCell1,
			Button,
			LoginIcon
		},
		methods:{
			reg(){
				let formData = new FormData();
				formData.append('username',this.username);
				formData.append('password',this.password);
				formData.append('nikename',this.nikename);
				let iconInputFile = document.querySelector('.headicon')
				formData.append('icon',iconInputFile.files[0])
				// console.log($('.headicon').files)
				axios({
					url:'/api/reg',
					method:"post",
					data:formData
				}).then(res=>{
					if(res.data.err==0){
						alert('');
						this.$router.push('/login')
					}else{
						this.errormes=res.data.msg;
					}
				})
			}
		},
		updated(){
		}
	}
</script>

<style scoped>
	.reg{
		margin-top: .84rem;
	}
	.tips{
		display: block;
		width: 100px;
		height: 20px;
		margin: 0 auto;
		color: red;
		line-height: 20px;
	}
</style>
