import Vue from 'vue';
import VueRouter from 'vue-router';
Vue.use(VueRouter);

import Home from '../pages/Home.vue'
import Classify from '../pages/Classify.vue'
import Find from '../pages/Find.vue'
import Mine from '../pages/Mine.vue'
import Login from '../pages/Login.vue'
import Reg from '../pages/Reg.vue'
import FindPassword from '../pages/FindPassword.vue'
import AddAddress from '../pages/Add-Address.vue'
import AddressCont from '../pages/Address-cont.vue'
import ModifyMes from '../pages/Modify-Mes.vue'
import Nopage from '../pages/NoPage.vue'
import ConfirmAddress from '../pages/ConfirmAddress.vue'
import Detail from '../pages/Detail.vue'
import EditAddress from '../pages/Edit-Address.vue'
let routes=[
	{path:'/home',component:Home},
	{path:'/classify',component:Classify},
	{path:'/find',component:Find},
	{path:'/find/:_id',component:Find},
	{path:'/mine',component:Mine},
	{path:'/login',component:Login},
	{path:'/reg',component:Reg},
	{path:'/detail',component:Detail},
	{path:'/findpassword',component:FindPassword},
	{path:'/add-address',component:AddAddress},
	{path:'/address-cont',component:AddressCont},
	{path:'/edit-address',component:EditAddress},
	{path:'/modify-mes',component:ModifyMes},
	{path:'/confirm-address',component:ConfirmAddress},
	{path:'/',redirect:'/home'},
	{path:'*',component:Nopage}
];

let router=new VueRouter({
	routes,
	mode:'history'
})
export default router