<template>
	<div class="menu" id="menu_bind">
		<ul class="menu-item">
			<li :class="{'active' :index+1 == selectItem }" v-for="(item,index) in data" @click="$emit('click',item,index)">
				{{item.title}}
			</li>
			<!-- <li class="active" v-for="(item,index) in data" >
				{{item.title}}
			</li> -->
		</ul>
	</div>
</template>

<script>
	export default {
		name:'classify-left',
		props:{
			selectItem:{
				type:Number,
				default:1
			}
		},
		data(){
			return {
				data:[],
			}
		},
		mounted(){
			axios({
				url:'/api/goods/goods3'
			}).then(res=>{
				if(res.data.err==0){
					this.data=res.data.data[0].goods3
				}
			});
		},
		methods:{
		}
	}
</script>

<style scoped>
	.menu {
	  flex: 1 1 24%;
	  width: 24%;
	  min-height: 100%;
	  background-color: #f8f8f8;
	  position: fixed;
	  overflow-y: auto;
	  -webkit-overflow-scrolling: touch;
	  height: 100%;
	  margin-top: 29px;
	}
	.menu .menu-item {
	  display: flex;
	  flex-direction: column;
	  border-right: 1px solid #f8f8f8;
	}
	.menu .menu-item li {
	  width: 100%;
	  height: .5rem;
	  line-height: .5rem;
	  text-align: center;
	  font-size: 12px;
	}
	.menu .menu-item li.active {
	  background-color: #fff;
	}
</style>
