<template>
	<div class="modify-mes">
		<top-blue :title="''"></top-blue>
		<uc-cell5></uc-cell5>
		<Button :val="''" @fn="modifynikeicon"></Button>
	</div>
</template>

<script>
	import TopBlue from '../components/top-blue.vue'
	import UcCell5 from '../components/uccell5.vue'
	import Button from '../components/button.vue'
	export default {
		name:'modify-mes',
		components:{
			Button,
			UcCell5,
			TopBlue
		},
		methods:{
			modifynikeicon(){
				let username=JSON.parse(localStorage.getItem('user')).data.username;
				let id=JSON.parse(localStorage.getItem('user')).data._id;
				let icon = document.getElementById('user-aver');
				let nikename = document.getElementById('user-name')
				axios({
					url:'/api/other/nikename',
					method:'post',
					data:{
						username:username,
						_id:id,
						icon:icon.value,
						nikename:nikename.value
					}
				}).then(res=>{
					if(res.data.err==0){
						console.log(res.data)
						this.$router.push('/mine');
					}else{
						alert('/')
					}
				})
			}
		}
	}
</script>

