<template>
	<div class="login">
		<top-blue :title="''"></top-blue>
		<login-icon></login-icon>
		<uc-cell1 :cont="''" v-model="username"></uc-cell1>
		<uc-cell1 :cont="''" :type="'password'" v-model="password"></uc-cell1>
		<Button :val="''" @fn="login"></Button>
		<div class="reg-login">
			<a href="javascript:;" class="fl" @click="ForgetPassword">?</a>
			<a href="javascript:;" class="fr" @click="Register"></a>
		</div>
	</div>
</template>

<script>
	import TopBlue from '../components/top-blue.vue'
	import UcCell1 from '../components/uccell1.vue'
	import Button from '../components/button.vue'
	import LoginIcon from '../components/Login-icon.vue'
	export default {
		name:'login',
		data(){
			return {
				username:'',
				password:'',
				errormes:'',
			}
		},
		components:{
			TopBlue,
			UcCell1,
			Button,
			LoginIcon
		},
		methods:{
			ForgetPassword(){
				this.$router.push('/findpassword')
			},
			Register(){
				this.$router.push('/reg')
			},
			login(){
				axios({
					url:'/api/login',
					data:{
						username:this.username,
						password:this.password,
					},
					method:'post'
				}).then(res=>{
					if(res.data.err==0){
						window.localStorage.setItem('user',JSON.stringify(res.data));
						if(this.$route.query.p){
							this.$router.replace(this.$route.query.p)
						}else{
							this.$router.replace('/mine')
						}
					}else{
						this.errormes=res.data.msg;
					}
				})
			}
		}
	}
</script>

<style scoped>
	.login{
		margin-top: .84rem;
	}
	.login .reg-login {
	  height: .2rem;
	  font-size: 14px;
	  margin: .05rem;
	}
	.login .reg-login a {
	  color: #2d9cff;
	}
</style>
