<template>
	<div class="category main-item">
		<h3></h3>
		 <div class="category-content" id="category_bind">
			 <div class="category-item" @click="show(item,index)" v-for="(item,index) in data">
				 <a href="javascript:;">
					 <img :src="item.icon" alt="">
					 <span class="category-name">{{item.title}}</span>
					 <div class="category-desc">
						 <!--<span class="category-name">{{item.business_name}}</span>-->
						 <span class="category-pri">{{item.price}}</span>
					 </div>
				 </a>
			 </div>
		 </div>
	</div>
</template>
<script>
	export default{
		name:'home-classify3',
		data(){
		    return {
				data:[]
			}	
		},
		methods:{
			show(item,index){
				
				this.$router.push({path:'/detail',query:{title:item.title,collection:"goods2"}})
			}
		},
		computed:{},
		components:{},
		mounted(){
			setTimeout(()=>{
				axios({
					url:"/api/goods/goods2"
				}).then(res=>{
					if(res.data.err==0){
						this.data=res.data.data
					}
				})
			},500)
		},
		updated(){},
		props:{}
	}
</script>
